<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <Navbar />
    <div class="container" style="padding-top: 65px;">
      <section class="row justify-content-center my-3">
        <div class="col-md-10">
          <h1 class="text-center mb-3">購物車</h1>
          <div class="row justify-content-center text-center">
            <div class="col-md-4">
              <div class="alert alert-rounded mb-0" role="alert"
                :class="{'alert-success':step == 1 ,'alert-light':step !== 1}">
                1.填寫訂單資料
              </div>
            </div>
            <div class="col-md-4">
              <div class="alert alert-rounded mb-0" role="alert"
                :class="{'alert-success':step == 2,'alert-light':step !== 2}">
                2.確認付款
              </div>
            </div>
            <div class="col-md-4">
              <div class="alert alert-rounded mb-0" role="alert"
                :class="{'alert-success':step == 3,'alert-light':step !== 3}">
                3.完成訂單！
              </div>
            </div>
          </div>
        </div>
      </section>
      <router-view :cart="cartData" @emitGetCar="getCar"></router-view>
    </div>
    <Footer />
  </div>
</template>

<script>
import Navbar from './Navbar';
import Footer from './Footer';

export default {
  data() {
    return {
      step: 1,
      cartData: {},
      isLoading: false,
    };
  },
  components: {
    Navbar,
    Footer,
  },
  methods: {
    getCar() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.cartData = response.data.data;
        vm.isLoading = false;
      });
    },
    setDefaultStep() {
      const step = localStorage.getItem('step') || 1;
      this.step = Number(step);
    },
    updateStep(step) {
      this.step = step;
      localStorage.setItem('step', step);
    },
  },
  created() {
    const vm = this;
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    vm.getCar();
    vm.setDefaultStep();
    vm.$bus.$on('step:update', (step) => {
      vm.updateStep(step);
    });
  },
};
</script>




