<template>
  <div>
    <Navbar />
    <!-- shop -->
    <router-view></router-view>
    <Footer />
    <!-- cart icon -->
    <section class="cart" title="購物車">
      <div class="cart-btn" @click="isShow = !isShow">
        <span>
          <i class="fas fa-shopping-cart cart-icon"></i>
          <span class="cart-num" v-if="this.cart.carts && this.cart.carts.length > 0">
            {{ cart.carts.length }}
          </span>
        </span>
      </div>
      <div class="cart-box" v-if="isShow">
        <div class="row justify-content-center bg-light border rounded py-3">
          <div class="col-md-12">
            <h3 class="h5">已選擇商品</h3>
            <div class="cart-close" @click="isShow = false">
              <span></span>
              <span></span>
            </div>
            <table class="table table-sm my-0">
              <tbody>
                <tr v-if="cart.total == 0">購物車還沒有東西哦！</tr>
                <tr v-for="(item, key) in cart.carts" :key="key">
                  <td>
                    <button type="button" class="btn bg-transparent btn-sm d-none d-md-block"
                      @click="removeCartItem(item.id)">
                      <i class="far fa-trash-alt"></i>
                    </button>
                  </td>
                  <td class="text-info cart-title">{{ item.product.title }}</td>
                  <td>{{ item.qty }}{{ item.product.unit }}</td>
                  <td class="text-right">{{ item.final_total | currency }}</td>
                </tr>
              </tbody>
            </table>
            <!-- button -->
            <router-link class="nav-item text-white" to="/carts/checkout_order"
              v-if="cart.total !== 0">
              <button type="button" class="btn btn-danger d-block mx-auto w-100"
              @click="isShow = !isShow">結帳去</button>
            </router-link>
            <button type="button" class="btn btn-info d-block text-white mx-auto w-100 mt-2"
            v-if="cart.total == 0" @click="isShow = !isShow">繼續購物</button>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Navbar from './Navbar';
import Footer from './Footer';

export default {
  data() {
    return {
      cart: {},
      isShow: false,
      isLoading: false,
    };
  },
  components: {
    Navbar,
    Footer,
  },
  methods: {
    getCart() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      this.$http.get(url).then((response) => {
        vm.cart = response.data.data;
      });
    },
    removeCartItem(id) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.getCart();
        vm.isLoading = false;
      });
    },
  },
  created() {
    this.getCart();
    this.$bus.$on('emitGetCart', this.getCart);
  },
};
</script>



