<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <section class="row justify-content-center my-5">
      <div class="col-md-8">
        <h2 class="text-center h3">訂單資訊</h2>
        <table class="table">
          <thead>
            <th></th>
            <th>品名</th>
            <th width="80">期限</th>
            <th width="80" class="text-right">售價</th>
          </thead>
          <tbody>
            <template v-if="cart.carts">
              <tr v-for="item in cart.carts" :key="item.id">
                <td class="align-middle text-center">
                  <button type="button" class="btn btn-outline-danger btn-sm"
                    @click="removeCartItem(item.id)">
                    <i class="far fa-trash-alt"></i>
                  </button>
                </td>
                <td class="align-middle">
                  {{ item.product.title }}
                  <div class="text-success" v-if="item.coupon">
                    已套用優惠券
                  </div>
                </td>
                <td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
                <td class="align-middle text-right">
                  <span :class="{ 'line-through': item.coupon }">
                    {{ item.total | currency }}
                  </span>
                  <div class="text-success mt-0" v-if="item.coupon">
                    {{ item.final_total | currency }}
                  </div>
                </td>
              </tr>
            </template>
          </tbody>
          <tfoot v-if="!isCartEmpty">
            <tr>
              <td colspan="3" class="text-right">總計</td>
              <td class="text-right">{{ cart.total | currency }}</td>
            </tr>
            <tr v-if="cart.final_total !== cart.total">
              <td colspan="3" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cart.final_total }}</td>
            </tr>
          </tfoot>
        </table>
        <div class="text-center mt-2" v-if="isCartEmpty">
          <i class="fa fa-shopping-cart fa-5" aria-hidden="true"></i>
          <h3 class="h5 font-weight-bold">你的購物車是空的</h3>
          <p>記得加入商品到你的購物車</p>
          <router-link class="btn btn-info d-inline-block" to="/">繼續購物</router-link>
        </div>
        <div class="input-group mb-2 input-group-sm" v-if="!isCartEmpty">
          <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼">
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">
              套用優惠碼
            </button>
          </div>
        </div>
      </div>
    </section>
    <CustomerInformation v-if="!isCartEmpty" />
  </div>
</template>

<style scoped>
.line-through {
  color: #bababa;
  text-decoration: line-through;
}
.fa-shopping-cart {
  font-size: 80px;
  color: #CCCCCC;
  display: block;
  padding: 10px 0;
}
</style>

<script>
import CustomerInformation from '../CustomerInformation';

export default {
  props: ['cart'],
  data() {
    return {
      step: 1,
      coupon_code: '',
      isLoading: false,
    };
  },
  components: {
    CustomerInformation,
  },
  methods: {
    removeCartItem(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.$emit('emitGetCar');
        vm.isLoading = false;
      });
    },
    addCouponCode() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const coupon = {
        code: vm.coupon_code,
      };
      this.$http.post(url, { data: coupon }).then((response) => {
        const couponMessage = response.data.message;
        if (response.data.success) {
          vm.$bus.$emit('alert:message', 'success', couponMessage);
          vm.$emit('emitGetCar');
        } else {
          vm.$bus.$emit('alert:message', 'warning', couponMessage);
        }
        vm.coupon_code = '';
      });
    },
  },
  computed: {
    isCartEmpty() {
      // this.cart.carts 是先判斷有讀取到cart 的值
      if (this.cart.carts && this.cart.carts.length === 0) {
        return true;
      }
      return false;
    },
  },
};
</script>




