<template>
  <div id="app">
    <router-view/>
    <vue-snotify/>
  </div>
</template>

<script>
export default {
  name: 'App',
  created() {
    this.$bus.$on('alert:message', this.alertMessage);
  },
  methods: {
    alertMessage(status, name) {
      this.$snotify[status](name, {
        timeout: 2000,
        showProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        position: 'rightCenter',
      });
    },
  },
};
</script>

<style lang="scss">
@import './assets/all.scss';
</style>



