<template>
  <nav class="navbar navbar-light bg-primary fixed-top">
    <loading :active.sync="isLoading"></loading>
    <div class="container">
      <router-link to="/" class="navbar-brand">JKTV 經典韓日劇</router-link>
      <router-link to="/login" class="btn btn-outline-dark">後台管理</router-link>
    </div>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      isLoading: false,
    };
  },
};
</script>



