<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="my-5 row justify-content-center">
      <div class="mb-5" v-if="order.is_paid">
        <i class="far fa-check-circle text-primary text-center"></i>
        <p class="h5 font-weight-bold">謝謝你，你的訂單已經成立</p>
      </div>
      <h2 class="text-center h3 col-12 mb-4">訂單資訊</h2>
      <form class="col-md-6" @submit.prevent="payOrder">
        <table class="table">
          <thead>
            <th>商名名稱</th>
            <th>期限</th>
            <th class="text-right">價格</th>
          </thead>
          <tbody>
            <tr v-for="item in order.products" :key="item.id">
              <td class="align-middle">{{ item.product.title }}</td>
              <td class="align-middle">{{ item.qty }} {{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total | currency }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="2" class="text-right">總計</td>
              <td class="text-right">{{ order.total | currency }}</td>
            </tr>
          </tfoot>
        </table>

        <table class="table">
          <tbody>
            <tr>
              <th width="120">顧客姓名</th>
              <td>{{ order.user.name }}</td>
            </tr>
            <tr>
              <th>顧客電話</th>
              <td>{{ order.user.tel }}</td>
            </tr>
            <tr>
              <th>顧客 Email</th>
              <td>{{ order.user.email }}</td>
            </tr>
            <tr>
              <th>付款狀態</th>
              <td>
                <span v-if="!order.is_paid" class="text-danger">尚未付款</span>
                <span v-else class="text-success">付款完成</span>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="text-right">
          <button class="btn btn-danger" v-if="!order.is_paid">確認付款去</button>
          <router-link to="/" class="btn btn-info d-inline-block" v-else>回購物首頁</router-link>
        </div>
      </form>
    </div>
  </div>
</template>

<style scoped>
.fa-check-circle {
  font-size: 80px;
  display: block;
  padding: 10px 0;
}
</style>

<script>

export default {
  data() {
    return {
      orderId: '',
      order: {
        user: {},
      },
      isLoading: false,
    };
  },
  methods: {
    getOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.order = response.data.order;
        vm.isLoading = false;
      });
    },
    payOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
      vm.isLoading = true;
      this.$http.post(url).then((response) => {
        if (response.data.success) {
          vm.isLoading = false;
          vm.$bus.$emit('step:update', 3);
          vm.setdefaultScroll();
          vm.getOrder();
        }
      });
    },
    setdefaultScroll() {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
  },
  created() {
    this.setdefaultScroll();
    this.orderId = this.$route.params.orderId;
    this.getOrder();
  },
};
</script>



