<template>
  <aside class="list-group sticky-top mb-5" style="top: 80px;">
    <a class="list-group-item list-group-item-action" href="#"
      :class="{'active': category === 'all'}"
      @click.prevent="updateCategory('all')">
      全部
    </a>
    <a href="#" class="list-group-item list-group-item-action"
      :class="{'active': category === '經典日劇'}"
      @click.prevent="updateCategory('經典日劇')">
      經典日劇系列
    </a>
    <a href="#" class="list-group-item list-group-item-action"
      :class="{'active': category === '口碑韓劇'}"
      @click.prevent="updateCategory('口碑韓劇')">
      口碑韓劇系列
    </a>
  </aside>
</template>

<script>
export default {
  data() {
    return {
      category: 'all',
    };
  },
  methods: {
    updateCategory(category) {
      this.category = category;
      this.$emit('emitCategory', category);
    },
  },
};
</script>




