import Vue from 'vue';
import Router from 'vue-router';

// admin
import Login from '@/components/admin/pages/Login';
import Products from '@/components/admin/pages/Products';
import Coupon from '@/components/admin/pages/Coupon';
import OrderList from '@/components/admin/pages/OrderList';
import CustomerOrders from '@/components/admin/pages/CustomerOrders';
import CustomerCheckout from '@/components/admin/pages/CustomerCheckout';
import Dashboard from '@/components/admin/Dashboard';

// client
import Home from '@/components/client/Home';
import Shop from '@/components/client/pages/Shop';
import Product from '@/components/client/pages/Product';
import Carts from '@/components/client/Carts';
import CheckoutOrder from '@/components/client/pages/CheckoutOrder';
import ConfirmPayment from '@/components/client/pages/ConfirmPayment';

Vue.use(Router);

export default new Router({
  routes: [
    {
      // 如果輸入不是router 路徑 ,導到 /
      path: '*',
      redirect: '/',
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      children: [
        {
          path: 'products', // 子路徑則不會加上 "/"
          name: 'Products',
          component: Products,
          meta: { requiresAuth: true },
        },
        {
          path: 'order_list',
          name: 'OrderList',
          component: OrderList,
          meta: { requiresAuth: true },
        },
        {
          path: 'coupon',
          name: 'Coupon',
          component: Coupon,
          meta: { requiresAuth: true },
        },
        {
          path: 'customer_orders',
          name: 'CustomerOrders',
          component: CustomerOrders,
        },
        {
          path: 'customer_checkout/:orderId',
          name: 'CustomerCheckout',
          component: CustomerCheckout,
        },
      ],
    },
    {
      path: '/',
      name: 'Home',
      component: Home,
      children: [
        {
          path: '',
          name: 'Shop',
          component: Shop,
        },
        {
          path: 'product/:itemId',
          name: 'Product',
          component: Product,
        },
      ],
    },
    {
      path: '/carts',
      name: 'Carts',
      component: Carts,
      children: [
        {
          path: 'checkout_order',
          name: 'CheckoutOrder',
          component: CheckoutOrder,
        },
        {
          path: 'confirm_payment/:orderId',
          name: 'ConfirmPayment',
          component: ConfirmPayment,
          props: true,
        },
      ],
    },
  ],
});



