<template>
  <section class="my-3 row justify-content-center">
    <form class="col-md-8" @submit.prevent="createOrder">
      <h2 class="text-center h3">訂購人資訊</h2>
      <div class="form-group">
        <label for="username">收件人姓名*</label>
        <input type="text" class="form-control" name="name" id="username"
          :class="{'is-invalid': errors.has('name')}"
          v-model="form.user.name" v-validate="'required'" placeholder="輸入姓名">
        <span class="text-danger" v-if="errors.has('name')">姓名必須輸入</span>
      </div>
      <div class="form-group">
        <label for="usertel">收件人電話*</label>
        <input type="tel" class="form-control" id="usertel" name="tel"
          :class="{'is-invalid': errors.has('tel')}"
          v-model="form.user.tel" v-validate="'required'" placeholder="請輸入電話">
        <span class="text-danger" v-if="errors.has('tel')">電話必須輸入</span>
      </div>
      <div class="form-group">
        <label for="useremail">Email*</label>
        <input type="email" class="form-control" name="email" id="useremail"
          :class="{'is-invalid': errors.has('email')}"
          v-model="form.user.email" v-validate="'required|email'" placeholder="請輸入 Email">
        <span class="text-danger" v-if="errors.has('email')">
          {{ errors.first('email')}}
        </span>
      </div>
      <div class="form-group">
        <label for="useraddress">留言</label>
        <textarea name="" id="" class="form-control" cols="30" rows="10"
          v-model="form.message"></textarea>
      </div>
      <div class="d-flex justify-content-between mt-3">
        <router-link class="btn btn-info d-inline-block" to="/">&lt;&lt; 繼續購物</router-link>
        <button class="btn btn-danger">送出訂單</button>
      </div>
    </form>
  </section>
</template>

<script>
export default {
  props: ['cart'],
  data() {
    return {
      coupon_code: '',
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
        },
        message: '',
      },
      step: 2,
    };
  },
  methods: {
    createOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(url, { data: order }).then((response) => {
            if (response.data.success) {
              vm.$bus.$emit('step:update', 2);
              vm.$router.push(`/carts/confirm_payment/${response.data.orderId}`);
            }
          });
        }
      });
    },
  },
};
</script>




