<template>
  <div class="login-block">
    <form class="form-signin" @submit.prevent="signin">
      <h1 class="h3 mb-3 font-weight-normal text-center">管理者登入</h1>
      <label for="inputEmail" class="sr-only">Email</label>
      <input type="email" id="inputEmail" class="form-control mb-2"
        v-model="user.username" placeholder="Email address" required autofocus>
      <label for="inputPassword" class="sr-only">Password</label>
      <input type="password" id="inputPassword" class="form-control"
        v-model="user.password" placeholder="Password" required>
      <button class="btn btn-lg btn-primary btn-block mt-3" type="submit">登入帳號</button>
      <div class="text-right mt-3">
        <router-link class="" to="/">
          返回首頁</router-link>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: {
        username: '',
        password: '',
      },
    };
  },
  methods: {
    signin() {
      const url = `${process.env.APIPATH}/admin/signin`;
      const vm = this;
      this.$http.post(url, vm.user).then((response) => {
        if (response.data.success) {
          vm.$router.push('/admin/products');
        }
      });
    },
  },
};
</script>

<style scoped>
.login-block {
  height: 100vh;
  display: flex;
  padding-bottom: 100px;
  background-color: #f5f5f5;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}

.form-signin .form-control {
  height: auto;
  padding: 10px;
  font-size: 16px;
}
</style>



