<template>
  <main style="padding-top: 65px;">
    <loading :active.sync="isLoading"></loading>
    <div class="container mt-4">
      <div class="row">
        <div class="col-md-6">
          <img :src="product.imageUrl" class="img-fluid" :alt="product.title">
        </div>
        <div class="col-md-6 my-4">
          <h1 class="h2">{{ product.title }}</h1>
          <hr>
          <span class="badge badge-info">
            {{ product.category }}
          </span>
          <p class="mt-2">{{ product.description }}</p>
          <div class="my-3">
            <del class="p-origin-price">{{ product.origin_price | currency }}</del>
            <p class="h4 p-price"> {{ product.price | currency }} </p>
          </div>
          <div class="input-group">
            <div class="row">
              <div class="col-md-6 mb-3">
                <select name="" class="form-control" v-model="product.num">
                  <option value="0" selected disabled>請選擇訂閱期限</option>
                  <option :value="num" v-for="num in 10" :key="num">
                    訂閱 {{num}} {{product.unit}}
                  </option>
                </select>
              </div>
              <div class="col-md-6">
                <button class="btn btn-primary btn-block"
                  @click.prevent="addtoCart(product)">
                  <i class="fa fa-cart-plus" aria-hidden="true"></i> 加入購物車
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row justify-content-center my-4">
        <div class="col-md-8">
          <h2 class="text-center">劇情簡介</h2>
          <p>{{ product.content }}</p>
          <div class="my-4">
            <router-link class="btn btn-info" to="/">
              &lt;&lt; 我要繼續逛</router-link>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {
    return {
      itemId: '',
      product: {},
      isLoading: false,
      cartData: {},
      num: 0,
    };
  },
  methods: {
    getProduct() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${vm.itemId}`;
      vm.isLoading = true;
      vm.$http.get(url).then((response) => {
        vm.product = response.data.product;
        vm.$set(vm.product, 'num', 0);
        vm.isLoading = false;
      });
    },
    addtoCart(product) {
      const vm = this;
      const currentQty = (product.num === 0) ? 1 : product.num;
      const alreadyCarts = vm.cartData.carts.filter(item => item.product_id === vm.product.id);
      if (alreadyCarts.length > 0) {
        vm.num = currentQty + alreadyCarts[0].qty;
        vm.removeCartItem(alreadyCarts[0], vm.addtoCartService);
      } else {
        vm.num = currentQty;
        vm.addtoCartService(product.id);
      }
    },
    removeCartItem(carts, cb) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${carts.id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.$emit('emitGetCar');
        cb(carts.product_id);
      });
    },
    addtoCartService(productId) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const cart = {
        product_id: productId,
        qty: vm.num,
      };
      vm.isLoading = true;
      vm.$http.post(url, { data: cart }).then(() => {
        vm.isLoading = false;
        vm.getCar();
        vm.$bus.$emit('emitGetCart');
        vm.$bus.$emit('alert:message', 'success', '已加入購物車');
      });
    },
    getCar() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      this.$http.get(url).then((response) => {
        vm.cartData = response.data.data;
      });
    },
  },
  created() {
    this.itemId = this.$route.params.itemId;
    this.getProduct();
    this.getCar();
  },
};
</script>



